.grid-container {
  display: grid;
  grid-template: 730px / auto auto auto;
  grid-gap: 10px;
  /* background-color: #00112b; */
  padding: 10px;
}
.grid-container>div {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding:20px 0;
  font-size: 30px;
}


